.header {
	border-bottom: 1px solid #f6f6f6;
	padding: 40px 0;
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.header__logo {
	display: flex;
}

.header__logo img {
	border-radius: 50%;
	margin-right: 15px;
}

.header__title {
	font-size: 22px;
	font-weight: 800;
	text-transform: uppercase;
}

@media screen and (max-width: 700px) {
	.header {
		padding: 20px 0;
	}
}

@media screen and (max-width: 412px) {
	.header {
		padding: 10px 0 30px 0;
	}
	.header__title {
		font-size: 18px;
	}
	.header__subtitle {
		font-size: 14px;
	}
}
