.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  margin: 0 auto;
  align-items: center;
  justify-content: center;
  z-index: 999;
  display: flex;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.4s, opacity 0.4s ease-in;
}

.popup_is-opened {
  visibility: visible;
  opacity: 1;
}

.popup__close-btn {
  position: absolute;
  right: -40px;
  top: -40px;
  width: 32px;
  height: 32px;
  background-image: url('../../assets/img/close.svg');
  cursor: pointer;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  background-color: transparent;
  border: none;
}

@media screen and (max-width: 560px) {
  .popup__close-btn {
    right: 0;
  }
}

@media screen and (max-width: 425px) {
  .popup__close-btn {
    width: 20px;
    height: 20px;
  }
}

.popup__container {
  background-color: #fff;
  border-radius: 10px;
  max-width: 600px;
  width: 100%;
  height: auto;
  padding: 34px 36px 37px;
  position: relative;
}

.popup__title {
  font-size: 22px;
  text-align: center;
  margin-bottom: 30px;
}

.popup__about-po {
  margin-bottom: 10px;
}

.popup__programs-info {
  display: flex;
  max-height: 500px;
  flex-direction: column;
  flex-wrap: wrap;
  align-content: space-between;
  /* width: 400px; */
}

.popup__programs-item {
  width: 100%;
  max-width: 140px;
  margin-bottom: 5px;
  background-color: #f3f3f3;
  border-radius: 10px;
  padding: 7px;
  font-size: 13px;
}
